.app-wrapper {
  display: grid;
  width: 1150px;
  margin: 0 auto;
  grid-template-areas:
          "h h"
          "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 1.5fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  background-color: #666B6E;
  grid-area: c;
}
